.Skills{
    clear: both;
    width: 100%;
    height: 1200px;
    background: #F0EEE2;
    .contents{
        .skill-chart{
            width: 100%;
            margin-top: 100px;
            display: flex;
            justify-content: space-around;
            .item-box{
                width: 100%;
                height: 100%;
                text-align: center;
                .item-label{                
                    font-size: 28px;
                }
                .item{
                    width: 60%;
                    margin: 30px auto;
                }
            }
        }
        .skill-list{
            display: flex;
            flex-wrap: wrap;
            justify-content: space-around;
            .iconBox{
                width: 140px;
                margin: 20px 60px;
                img{
                    width: 100%;
                }
            }
        }
    }
    
    
}