.Footer{
    clear: both;
    width: 100%;
    height: 140px;
    background: #39424C;
    .left{
        width: calc(50% - 60px);
        float: left;
        padding: 20px;
    }
    .right{
        width: calc(50% - 60px);
        float: right;
        padding: 20px;
    }
    p{
        font-size: 14px;
        margin: 8px 0;
        color: #eee;
    }
    a{
        text-decoration: none;        
        color: #fff;
    }
    a:visited{
        color: #fff;
    }
}