.AboutMe{
    clear: both;
    width: 100%;
    background: #F0EEE2;
    height: 500px;
    .contents{
        width: 100%;
        margin-top: 100px;
        display: flex;
        justify-content: space-around;
        .item-box{
            width: 180px;
            height: 180px;
            border-radius: 50%;
            background: #fff;
            position: relative;
            .item-label{
                position: absolute;
                left: -15px;
                top: -5px;
                width: 100px;
                padding: 3px;
                text-align: center;
                color: #fff;
                background: #2496ED;
            }
            .item{
                position: relative;
                top: 30px;
                width: 100%;
                height: 100%;
                text-align: center;
                .item-image{
                    img{
                        width: 50%;
                    }
                }
                .item-text{
                    font-size: 16px;
                }
                .item-text.font14{font-size: 14px;}
                .item-text.font12{font-size: 12px;}
                    
            }
        }
    }
}