.Record{
    clear: both;
    width: 100%;
    height: 700px;
    background: #fff;
    .contents{
        margin: 20px 0;
        padding: 0 10%;
    }
    .record{
        margin-top: 50px;
        .record_title{
            width: 100%;
            font-size: 28px;
            font-weight: 700;
            text-align: center;
            margin: 25px;
        }        
        .record_area{
            .contents{
                display: flex;
                flex-direction: column;
                align-items: center;
            }
        }
        .record_div_area{
            display: flex;
            justify-content: space-around;
            align-items: center;
            color: #4C4C4C;
            .contents{
                display: flex;
                justify-content: center;
                img{
                    width: 200px;
                }                
                .content_left{
                    font-size: 28px;
                    margin: auto 40px;
                }
                .content_right{
                    text-align: center;
                }
            }
        }
    }
    a{
        color: #4C4C4C;
    }
    a:hover{
        color:mediumblue;
    }
}
