.Top{
    width: calc(100% - 60px);
    height: 80px;
    line-height: 50px;
    padding: 10px 30px;
    background: #eee;
    float: left;
    .leftTop{
        width: 35%;
        height: 100%;
        float: left;
        h2{
            height: 100%;
            font-size: 24px;
            line-height: 80px;
            text-align: left;
            color: #4C4C4C;
        }
    }
    .rightTop{
        width: 65%;
        height: 100%;
        float: left;
    }
}

