.wrapper{
    clear: both;
    width: 100%;
    height: 100%;
    .App{
        background: #eee;
        padding: 0 30px;
        display: flex;
        flex-direction: column;
        div{
            .title{
                width: 100%;
                padding-top: 50px;
                h3{                        
                    text-align: center;
                    font-size: 38px;
                    line-height: 48px;
                    display: block;
                }
                .logo{
                    font-size: 40px;
                }
            }   
        }
        a{
            text-decoration: none;    
        }
        a:visited {
            color: #4C4C4C;
        }        
    }
    
}
